import './PedidoRastreo.css'

import { IonModal, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { AiOutlineQrcode } from 'react-icons/ai'
import { BsBag, BsChatDots } from 'react-icons/bs'

import { CalificacionTransportista, Page } from '../../components'
import ChatModal from '../../components/Chat/ChatModal'
import { SubHeader } from '../../components/SubHeader'
import { PEDIDO_ESTADOS, RUTAS } from '../../constants'
import { plata_fmt } from '../../helpers'
import { DetailViewProps } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Pedido } from '../../services/api'
import moment from 'moment'

const QRCode = require('qrcode.react')

interface EstadoPedidoProps {
  idPedido: number
}

