export const PedidoRastreo: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { tarjetaStore, ciudades } = rootStore
  const [Pedido, setPedido] = useState<Pedido>()
  const [MedioPago, setMedioPago] = useState('')
  const [ModalCodigoQR, setModalCodigoQR] = useState(false)
  const [Contrasena, setContrasena] = useState('123456')
  const [Loading, setLoading] = useState(true)
  const [Visible, setVisible] = useState(false)
  const [CalificarDisabled, setCalificarDisabled] = useState(false)
  const [ChatVisible, setChatVisible] = useState(false)
  const Movil = window.innerWidth <= 600
  const idPedido = match.params.id
  const intervalRef = useRef(null as any)
  const [CalificacionModalVisible, setCalificacionModalVisible] = useState(false)

  useEffect(() => {
    if (idPedido) {
      obtenerPedido(idPedido)
      obtenerContrasenaPedido(idPedido)
    }
  }, [match])

  useEffect(() => {
    if (!Pedido) {
      return
    }
    if (Pedido.estado >= PEDIDO_ESTADOS.pagado && Pedido.estado !== PEDIDO_ESTADOS.entregado) {
      intervalRef.current = setInterval(() => refrescarPedido(), 10000)
    }
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current)
      }
    }
  }, [Pedido])

  useEffect(() => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    setChatVisible(currentUrlParams.get('chat') === '1')
  }, [window.location.search])

  const obtenerContrasenaPedido = (idPedido: number) => {
    rootStore.environment.api.getPedidoContrasena(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setContrasena(result.data.contrasena)
        setVisible(true)
      }
    })
  }

  const refrescarPedido = () => {
    rootStore.environment.api.getPedido(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setPedido(result.pedido)
        setCalificarDisabled(!!result.pedido.calificacion_transportista)
      }
    })
  }

  const obtenerPedido = (idPedido: number) => {
    rootStore.environment.api.getPedido(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setLoading(false)
        setPedido(result.pedido)
        setCalificarDisabled(!!result.pedido.calificacion_transportista)
        // @ts-ignore
        if (result.pedido.tarjeta) {
          const tarjeta = tarjetaStore.getTarjeta(result.pedido.tarjeta.card_id)
          setMedioPago(tarjeta ? tarjeta.shortName : 'Tarjeta de crédito')
        } else if (result.pedido.medio_pago === 1) {
          setMedioPago('Tarjeta ocasional')
        } else if (result.pedido.medio_pago === 2) {
          setMedioPago('Zimple')
        } else if (result.pedido.medio_pago === 3) {
          setMedioPago('Tigo Money')
        } else if (result.pedido.medio_pago === 4) {
          setMedioPago('Billetera personal')
        } else if (result.pedido.medio_pago === -1) {
          setMedioPago('Test')
        }
      }
    })
  }

  const seleccionarObjetivoCalificacion = (tipo_calificacion: number) => {
    if (Pedido) {
      let nombre = ''
      let imagen = ''
      let id_calificar = 0
      if (tipo_calificacion === 30) {
        // Transportista
        nombre = Pedido.transportista.usuario.first_name + ' ' + Pedido.transportista.usuario.last_name
        imagen = Pedido.transportista.usuario.imagen_perfil
        id_calificar = Pedido.transportista.id
      } else if (tipo_calificacion === 20) {
        // Fabricante
        const fabricante = Pedido.fabricante as any
        nombre = fabricante.nombre
        imagen = fabricante.imagen_perfil
        id_calificar = fabricante.id
      }
      const itinerarioCalificar = {
        tipo_calificacion: tipo_calificacion,
        id_pedido: Pedido.id,
        nombre: nombre,
        imagen: imagen,
        id_calificar: id_calificar,
      }
      rootStore.setPedidoCalificar(itinerarioCalificar)
      setCalificacionModalVisible(true)
    }
  }

  const tiempoEntregaRestante = (fecha_entrega_estimada: string) => {
    let diff = moment(fecha_entrega_estimada).diff(moment())
    diff = moment.duration(diff).asMinutes()
    return Math.round(diff < 1 ? 0 : diff)
  }

  return (
    <Page login_required solo_cliente>
      <IonModal
        isOpen={ModalCodigoQR}
        cssClass="modal-perdil-cliente-short"
        onDidDismiss={() => setModalCodigoQR(false)}
      >
        <div
          style={{
            height: '100%',
          }}
        >
          <div className="heather-info-registro background-color-naranja-abi">
            <div>
              <span>Contraseña del pedido</span>
            </div>

            <div onClick={() => setModalCodigoQR(false)}>
              <FIcon.XCircle style={{ width: 26, marginRight: 5, color: '#fff' }} />
            </div>
          </div>
          <div
            style={{
              height: '100%',
              width: '100%',
              display: 'flex',
              alignItems: 'center',
              flexDirection: 'column',
              justifyContent: 'center',
            }}
          >
            <QRCode value={Contrasena} size={180} />
            <h1 className={'pt-2'} style={{ color: 'black' }}>
              <strong>{Contrasena}</strong>
            </h1>
          </div>
        </div>
      </IonModal>
      {CalificacionModalVisible && (
        <CalificacionTransportista
          color="var(--naranja-abi)"
          visible={CalificacionModalVisible}
          onAceptar={() => {
            setCalificacionModalVisible(false)
            setCalificacionModalVisible(false)
            setCalificarDisabled(true)
          }}
          onCerrar={() => {
            setCalificacionModalVisible(false)
          }}
        />
      )}

      <SubHeader links={[{ titulo: 'Pedidos', ruta: RUTAS.PEDIDOS }, { titulo: 'Estado de tu pedido' }]} />
      {Loading && (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', height: 500 }}>
          <IonSpinner name="crescent" />
        </div>
      )}
      {Pedido && !Loading && (
        <div
          className="estado-pedido-container"
          style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-between' }}
        >
          <img className="imagen-camion-rastreo" src={'assets/icon/truck_orange.png'} />
          {estadoPedido(Pedido)}
          {Pedido.estado <= PEDIDO_ESTADOS.enviado && <div className='text-center mt-4'>
            <p>
              Tiempo estimado de entrega<br/>
              <span style={{fontWeight: 'bold', fontSize: 24}}>
                <FIcon.Clock style={{ width: 32, height: 32, marginRight: 10, color: 'var(--gris-oscuro-abi)' }} />
                {tiempoEntregaRestante(Pedido.fecha_entrega_estimada)} min
              </span>
            </p>
          </div>}
          <div className="detalle-pedido-tracking">
            <div style={{ display: 'flex', justifyContent: 'center' }} className="fila-detalle-tracking">
              <div className="icono-direccion-tracking">
                <img style={{ height: '100%' }} src={'assets/icon/ubicacion-gray.png'} />
              </div>

              <div className="detalle-texto-tracking">
                <p style={{ fontWeight: 'bold', color: 'var(--verde-claro)' }}>{Pedido.direccion.nombre}</p>
                <p>
                  {Pedido.direccion.calle_principal} {Pedido.direccion.numero} casi {Pedido.direccion.calle_secundaria}
                </p>
                <p>{ciudades.find((ciudad) => ciudad.id === parseInt(Pedido.direccion.ciudad))?.nombre}</p>
              </div>
            </div>
            <div style={{ display: 'flex' }} className="fila-detalle-tracking">
              <div className="icono-direccion-tracking">
                <BsBag size={Movil ? 40 : 40} />
              </div>
              <div className="detalle-texto-tracking">
                <p style={{ fontWeight: 'bold', color: 'var(--verde-claro)' }}>Total: {plata_fmt(Pedido.total)}</p>
                <p>
                  Método de pago: <span className="medio-pago-tracking">{MedioPago}</span>
                </p>
              </div>
            </div>
          </div>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }} id="chat-footer-tracking">
            {Pedido.estado && Pedido.estado === PEDIDO_ESTADOS.enviado && (
              <>
                <a className="acccion-pedido-tracking" onClick={() => setChatVisible(true)}>
                  <BsChatDots
                    className="icono-chat-tracking"
                    style={{ color: 'var(--rojo-abi)' }}
                    size={Movil ? 20 : 25}
                  />
                  <div>Contacta con tu Driver ABi</div>
                </a>
                <a className="acccion-pedido-tracking" onClick={() => setModalCodigoQR(true)}>
                  <AiOutlineQrcode className="icono-chat-tracking" size={Movil ? 20 : 25} />
                  <div>Código del pedido</div>
                </a>
              </>
            )}
            {Pedido.estado === PEDIDO_ESTADOS.entregado && (
              <div className="finalizar-compra-container">
                <button
                  className="btn btn-info px-5 py-2 mt-2"
                  type="button"
                  disabled={CalificarDisabled}
                  onClick={() => seleccionarObjetivoCalificacion(30)}
                >
                  Calificar Driver
                </button>
              </div>
            )}
          </div>
        </div>
      )}
      {!!Pedido && <ChatModal visible={ChatVisible} setVisible={setChatVisible} pedido={Pedido} />}
    </Page>
  )
})

export default PedidoRastreo
