import { Capacitor } from '@capacitor/core'
import { OneSignal as OneSignalCapacitor } from '@ionic-native/onesignal'
import { StatusBar } from '@ionic-native/status-bar'
import { IonSpinner } from '@ionic/react'
import { Carousel } from '@trendyol-js/react-carousel'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import 'swiper/bundle'
import { Swiper, SwiperSlide } from 'swiper/react'
import { CarouselProductos } from '../../components/CarouselProductos'
import Page from '../../components/Page'
import { PEDIDO_ESTADOS, RUTAS } from '../../constants'
import { formatTextoURL, useConfirm } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Pedido } from '../../services/api'
import './Home.css'
import { estadoPedido } from './PedidoRastreo'

const { BASE_URL } = require('../../config/env')

export const Home: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore, carritoStore, chatStore, favoritoStore } = rootStore
  const [seconds, setSeconds] = useState(0)
  const [minutes, setMinutes] = useState(1)
  const [Horas, setHoras] = useState(20)
  const [Movil, setMovil] = useState(false)
  const [ProductosOportuindadDia, setProductosOportuindadDia] = useState(null as any)
  const [ProductosMasVistos, setProductosMasVistos] = useState(null as any)
  const [ProductosMasVendidos, setProductosMasVendidos] = useState(null as any)
  const [UltimoPedido, setUltimoPedido] = useState<Pedido | null>()
  const [Loading, setLoading] = useState(true)
  const [FabricantesBanner, setFabricantesBanner] = useState(null as any)

  useEffect(() => {
    const token = setTimeout(updateTime, 1000)

    return function cleanUp() {
      clearTimeout(token)
    }
  }, [])

  useEffect(() => {
    if (Capacitor.getPlatform() === 'android') {
      StatusBar.backgroundColorByHexString('#ffa101')
    } else if (Capacitor.getPlatform() === 'ios') {
      document.body.style.background = '#ffa101'
    }
    setMovil(window.innerWidth <= 500)
    obtenerDatosInicio()

    const fabricantesBanner = rootStore.fabricantesVistaCliente
      .filter((fab) => !!fab.imagen_perfil)
      .map((fabricante, index) => (
        <div
          key={index}
          className={'cpointer'}
          onClick={() => {
            history.push(`${RUTAS.BUSCAR}${'?f=' + fabricante.id}`)
          }}
        >
          <img
            src={fabricante.imagen_perfil || ''}
            alt={'Logo de ' + fabricante.nombre}
            className={'imagen-perfil'}
            style={{ height: '90%', maxHeight: '90%', width: '90%', maxWidth: '90%', objectFit: 'contain' }}
          />
        </div>
      ))
    fabricantesBanner.push(
      <div
        key={9999}
        className={'cpointer btn-unirse'}
        onClick={() => {
          history.push(RUTAS.REGISTRO_FABRICANTE)
        }}
      >
        <FIcon.Plus size={45} color={'#303AB2'} />
        <span className={'text-primary'}>UNIRSE</span>
      </div>,
    )
    setFabricantesBanner(fabricantesBanner)
  }, [])

  React.useEffect(() => {
    const intervalID = setInterval(() => {
      const mright = document.getElementById('marca-right')
      mright && mright.click()
    }, 7000)
    return () => clearInterval(intervalID)
  }, [])

  const obtenerDatosInicio = async () => {
    const data = await rootStore.actualizarDatosInicio(false)
    if (data) {
      setHoras(data.horas_restantes)
      setMinutes(data.minutos_restantes)
      setUltimoPedido(data.ultimo_pedido)
      setProductosOportuindadDia(data.compra_mejorada)
      setProductosMasVistos(data.mas_vistos)
      setProductosMasVendidos(data.mas_vendidos)
    }
    setLoading(false)
  }

  const updateTime = () => {
    if (Horas == 0 && minutes == 0 && seconds == 0) {
      setSeconds(0)
      setMinutes(5)
      setHoras(3)
    } else {
      if (minutes == 0 && seconds == 0) {
        setHoras((horas) => horas - 1)
        setMinutes(59)
        setSeconds(59)
      } else if (seconds == 0) {
        setMinutes((minutes) => minutes - 1)
        setSeconds(59)
      } else {
        setSeconds((seconds) => seconds - 1)
      }
    }
  }

  return (
    <Page id="home-cliente" login_required={false} solo_cliente>
      {/* Contenido de la página*/}
      {Loading && (
        <div style={{ height: 500, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <IonSpinner color={'primary'} name="crescent" />
        </div>
      )}
      {!Loading && (
        <div id={'page-home'} className="page-wrapper" style={{ display: 'flex' }}>
          <div className="container-lg" style={{ backgroundColor: '#fff' }}>
            <div className={'content-container row mx-0 px-0'} style={{ backgroundColor: '#fff' }}>
              <div className={'col-12 mx-0 p-0'}>
                <div className="">
                  <Swiper
                    className={'categorias-home-swiper'}
                    observer
                    initialSlide={0}
                    navigation={Movil ? false : true}
                    scrollbar={{ hide: false }}
                    slidesPerView={'auto'}
                    autoplay={{
                      delay: 3500,
                      disableOnInteraction: false,
                    }}
                  >
                    {rootStore.categoriasWeb
                      .filter((cat) => !!cat.imagen)
                      .map((categoria, index) => {
                        return (
                          <SwiperSlide key={index}>
                            <a>
                              <div
                                style={{
                                  display: 'flex',
                                  flexDirection: 'column',
                                  justifyContent: 'center',
                                  alignContent: 'center',
                                  height: '100%',
                                  alignItems: 'center',
                                }}
                                onClick={() => {
                                  history.push(
                                    RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, formatTextoURL(categoria.nombre)),
                                  )
                                }}
                              >
                                {categoria.imagen ? (
                                  <div
                                    style={{
                                      backgroundImage: `url(${categoria.imagen})`,
                                      backgroundSize: 'auto 100%',
                                      overflow: 'hidden',
                                      display: 'flex',
                                      alignItems: 'flex-end',
                                      justifyContent: 'center',
                                    }}
                                    className={'imagen-carrosel-categorias '}
                                  ></div>
                                ) : null}

                                <span className="nombre-categoria-carusel">{categoria.nombre}</span>
                              </div>
                            </a>
                          </SwiperSlide>
                        )
                      })}
                  </Swiper>
                </div>

                <div className="d-flex justify-content-center cpointer" onClick={() => history.push(RUTAS.CATEGORIAS)}>
                  <span className="footer-carousel-productos">+ Ver más</span>
                </div>
                {!!UltimoPedido &&
                  UltimoPedido.estado !== PEDIDO_ESTADOS.entregado &&
                  UltimoPedido.estado !== PEDIDO_ESTADOS.incidente && (
                    <a onClick={() => history.push(RUTAS.RASTREAR_PEDIDO(UltimoPedido.id))}>
                      {estadoPedido(UltimoPedido)}
                    </a>
                  )}
                {ProductosOportuindadDia !== null && (
                  <CarouselProductos Movil={Movil} titulo={'Promociones'} productos={ProductosOportuindadDia} />
                )}
                {ProductosMasVistos !== null && (
                  <CarouselProductos Movil={Movil} titulo={'Lo más visto'} productos={ProductosMasVistos} />
                )}
                {ProductosMasVendidos !== null && (
                  <CarouselProductos Movil={Movil} titulo={'Lo más vendido'} productos={ProductosMasVendidos} />
                )}
                <div className="carousel-marcas-container" style={{ marginBottom: '60px' }}>
                  <div className="d-flex justify-content-center">
                    <span className="titulo-carousel">Marcas adheridas</span>
                  </div>
                  <div style={{ height: Movil ? 110 : 210 }}>
                    {FabricantesBanner && (
                      <Carousel
                        show={Movil ? 3 : 5}
                        slide={Movil ? 1 : 2}
                        swiping={true}
                        useArrowKeys={true}
                        className={'carousel-marcas'}
                        leftArrow={
                          <div id={'marca-left'} className={'arrow-container'}>
                            <FIcon.ChevronLeft size={30} color={'var(--gris-oscuro-abi)'} />
                          </div>
                        }
                        rightArrow={
                          <div id={'marca-right'} className={'arrow-container'}>
                            <FIcon.ChevronRight size={30} color={'var(--gris-oscuro-abi)'} />
                          </div>
                        }
                      >
                        {FabricantesBanner as any}
                      </Carousel>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default Home
